
.center {
    float: none;
    margin: 0 auto;
}

  /* Sticky footer styles
-------------------------------------------------- */
html {
    position: relative;
    min-height: 100%;
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    /* Set the fixed height of the footer here */
    height: 60px;
    background-color: #f5f5f5;
}
body>.container {
    padding: 60px 15px 0;
}
.container .text-muted {
    margin: 20px 0;
}
.footer>.container {
    padding-right: 15px;
    padding-left: 15px;
}
code {
    font-size: 80%;
}
#app {
    padding-top: 60px;
    margin-bottom: 60px;
}






























































































































































































































































































































































































































































































































































































































































































